/* Tailwind */
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

/* Typo */
@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,,600,700,800&subset=latin-ext,vietnamese');
@import url('https://fonts.googleapis.com/css?family=Montserrat+Alternates:400,500,600,700,800&subset=latin-ext,vietnamese');

* {
  font-size: calc(10px + 1vmin);
  font-family: 'Montserrat';
  font-weight: 400;
  color: #21202b;
  user-select: none;
}

h1 {
  font-family: 'Montserrat Alternates';
  color: white;
  font-size: calc(50px + 1vmin);
  align-self: center;
}

h2 {
  font-size: calc(35px + 1vmin);
  cursor: default;
}

h2 b {
  font-size: calc(35px + 1vmin);
  font-weight: 600;
}

h3 {
  font-size: calc(30px + 1vmin);
}

.h3 {
  font-size: calc(22px + 1vmin);
  font-weight: 500;
  cursor: default;
}

h4 {
  font-size: calc(17px + 1vmin);
  cursor: default;
}

h5 {
  font-size: calc(15px + 1vmin);
  font-weight: 500;
}

b {
  font-weight: 600;
  font-size: calc(17px + 1vmin);
}

p b {
  font-weight: 600;
  font-size: calc(10px + 1vmin);
}

p.sm {
  font-size: calc(6px + 1vmin);
  cursor: default;
}

.sm {
  font-size: calc(8px + 1vmin);
}

p.sm b {
  font-size: calc(6px + 1vmin);
  font-weight: 600;
}

text {
  font-size: calc(8px + 1vmin);
  font-weight: 400;
}

text.sm {
  font-size: calc(6px + 1vmin);
  font-weight: 300;
}

/* smaller pc screens */
@media screen and (max-width: 1500px) {
  h1 {
    font-size: calc(30px + 1vmin);
  }
}

/* General */
.App {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #21202b;
  justify-content: center;
}